<template>
  <div id="forgotPassword">
    <div class="wrapper">
      <h3 class="pageTitle">{{ title }}</h3><br>
  		<form class="appForm" v-on:submit.prevent="resetPasswordSubmit">
  			<p class="textCenter formError errorText" v-if="errorText">{{ errorText }}</p>
  			<div class="formGroup">
  				<input type="password" v-model="password" :class="{ errorText: isPasswordError }" class="appInput loginInput" placeholder="Enter New Password">
  			</div>
        <div class="formGroup">
          <input type="password" v-model="confirmPassword" :class="{ errorText: isConfirmPasswordError }" class="appInput loginInput" placeholder="Re-enter New Password">
        </div>
  			<div class="formGroup">
  				<button type="submit" class="appPrimaryBtn btnFulWidth">Submit</button>
  			</div>
  		</form>
  		<ul class="appBottomLinks">
  			<li><a href="#/">Back to Login</a></li>
  			<li><a href="#/signup">Create new account</a></li>
  		</ul>
  	</div>
  </div>
</template>

<script>

import Vue from 'vue'

export default {
  name: 'ResetForgotPassword',
  props: ['userId'],
  data () {
    return {
      title: 'Reset Password',
  		password: '',
      confirmPassword: '',
  		isError: false,
  		errorText: '',
      isPasswordError: false,
  		isConfirmPasswordError: false,
      apiUrl: Vue.config.apiUrl,
    }
  },
  methods: {

  	resetPasswordSubmit: function() {

      this.isError = false;
      this.errorText = '';
      var data = {};
  		if(!this.password) {
  			this.isError = this.password = true;
  		} else {
        data.password = this.password;
  			this.isPasswordError = false;
  		}

      if(!this.confirmPassword) {
        this.isError = this.isConfirmPasswordError = true;
      } else if(this.password != this.confirmPassword) {
        this.isError = this.isConfirmPasswordError = true;
      } else {
        this.isConfirmPasswordError = false;
      }

  		if(this.isError) return false;
      else data.userId = this.userId;

      this.$store.commit('setIsPageLoading', true);
  		this.$http.post(this.apiUrl + 'restForgotPassword', data).then(response => {
            this.$store.commit('setIsPageLoading', false);
            var body = response.body;
            if(body.status == 'success') {
              alert('Your password has been reset, you can login now');
              window.location.href = '#/';

            } else if(body.status == 'error') {
                alert(body.message);
                this.password = this.password = '';
            }
        }).catch(function(error) {
          this.$store.commit('setIsPageLoading', false);
          alert(error.statusText);
        })
  	}
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #forgotPassword {
    margin-top: 15%;
  }
</style>



