import Vue from 'vue'
import Router from 'vue-router'
import store from '../store'

import Login from '@/components/Login'
import Register from '@/components/Register'
import ForgotPassword from '@/components/ForgotPassword'
import ResetForgotPassword from '@/components/ResetForgotPassword'
import Dashboard from '@/components/Dashboard'
import EditProfile from '@/components/EditProfile'
import ChoosePassword from '@/components/ChoosePassword'
import BloodBank from '@/components/BloodBank'
import Profiles from '@/components/Profiles'
import Profile from '@/components/Profile'
import Occupations from '@/components/Occupations'
import SearchProfiles from '@/components/SearchProfiles'
import About from '@/components/About'
import NotAvailable from '@/components/NotAvailable'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/signup',
      name: 'Register',
      component: Register
    },
    {
      path: '/forgotPassword',
      name: 'ForgotPassword',
      component: ForgotPassword
    },
    {
      path: '/resetForgotPassword/:userId',
      name: 'ResetForgotPassword',
      props: true,
      component: ResetForgotPassword
    },
    {
      path: '/dashboard',
      name: 'Dashboard',
      component: Dashboard
    },
    {
      path: '/editProfile',
      name: 'EditProfile',
      component: EditProfile
    },
    {
      path: '/choosePassword',
      name: 'ChoosePassword',
      component: ChoosePassword
    },
    {
      path: '/bloodBank',
      name: 'BloodBank',
      component: BloodBank
    },
    {
      path: '/profiles/:section/:id',
      name: 'Profiles',
      props: true,
      component: Profiles
    },
    {
      path: '/profile/:id',
      name: 'Profile',
      props: true,
      component: Profile
    },
    {
      path: '/occupations',
      name: 'Occupations',
      component: Occupations
    },
    {
      path: '/searchProfiles',
      name: 'SearchProfiles',
      component: SearchProfiles
    },
    {
      path: '/about/:who',
      name: 'About',
      props: true,
      component: About
    },
    {
      path: '/notAvailable',
      name: 'NotAvailable',
      component: NotAvailable
    }
  ]
});

router.beforeEach((to, from, next) => {
  router.app.$store.commit('setIsPageLoading', true);
  router.app.$emit('menuClickEvent', false);
  router.app.$emit('moreMenuClick', false);
  var allowed = ['Login', 'Register', 'ForgotPassword', 'ResetForgotPassword'];
  // console.log(to);
  if(!store.getters.isUserActive && allowed.indexOf(to.name) == -1) {
    next('/');
  } else if(store.getters.isUserActive && to.name == 'Login') {
    if(confirm('Do you want to logout?')) {
      store.commit('setIsPageLoading', true);
      Vue.http.post(Vue.config.apiUrl + 'logout', {}, {
        headers: {
          'userToken': store.getters.userAccessToken
        }
      }).then(response => {
        store.commit('setIsPageLoading', false);
          var body = response.body;
          if(body.status == 'success') {
              store.commit('setUserAccessToken', '');
              store.commit('setIsUserActive', false);
              store.commit('setProfiles', []);
              window.location.href = '#/';

          } else if(body.status == 'error') {
              alert(body.message); return false;
          }
      }).catch(function(err){
        store.commit('setIsPageLoading', false);
        console.log('error', err);
        alert('Something went wrong, please try again'); return false;
      })
    }
  } else {
    next();
  }


});

router.afterEach((to, from, next) => {
  router.app.$store.commit('setIsPageLoading', false);
});

export default router


