<template>
  <div id="forgotPassword">
    <div class="wrapper">
      <h3 class="pageTitle">{{ title }}</h3><br>
  		<form class="appForm" v-on:submit.prevent="forgotPasswordSubmit">
  			<p class="textCenter formError errorText" v-if="errorText">{{ errorText }}</p>
        <p class="forgotDescription">Please enter your email address registered with HomeCity</p>
  			<div class="formGroup">
  				<input type="text" v-model="username" :class="{ errorText: isUsernameError }" value="" class="appInput loginInput" placeholder="Email Address">
  			</div>
        <p class="forgotDescription" v-if="isEmailSent">We've sent a verification code to your email. Check the spam list if not found, enter the code below and submit again.</p>
        <div class="formGroup" v-if="isEmailSent">
          <input type="text" v-model="code" :class="{ errorText: isCodeError }" value="" class="appInput loginInput" placeholder="Verification Code">
        </div>
  			<div class="formGroup">
  				<button type="submit" class="appPrimaryBtn btnFulWidth">Submit</button>
  			</div>
  		</form>
  		<ul class="appBottomLinks">
  			<li><a href="#/">Back to Login</a></li>
  			<li><a href="#/signup">Create new account</a></li>
  		</ul>
  	</div>
  </div>
</template>

<script>

import Vue from 'vue'

export default {
  name: 'ForgotPassword',
  data () {
    return {
      title: 'Forgot Password',
  		username: '',
      isEmailSent: false,
      code: '',
  		isError: false,
  		errorText: '',
      isUsernameError: false,
  		isCodeError: false,
      apiUrl: Vue.config.apiUrl,
    }
  },
  methods: {

  	forgotPasswordSubmit: function() {

      this.isError = false;
      this.errorText = '';
      var data = {};
  		if(!this.username || !this.validateEmail(this.username)) {
  			this.isError = this.isUsernameError = true;
  		} else {
        data.email = this.username;
  			this.isUsernameError = false;
  		}

      if(this.isEmailSent) {
        if(!this.code) {
          this.isError = this.isCodeError = true;
        } else {
          data.code = this.code;
          this.isCodeError = false;
        }
      }

  		if(this.isError) return false;
      this.$store.commit('setIsPageLoading', true);
  		this.$http.post(this.apiUrl + 'forgotPassword', data).then(response => {
            this.$store.commit('setIsPageLoading', false);
            var body = response.body;
            if(body.status == 'success') {
              if(this.isEmailSent)
                window.location.href = '#/resetForgotPassword/' + body.userId;
              else {
                this.isEmailSent = true;
              }

            } else if(body.status == 'error') {
                this.isError = true;
                this.errorText = body.message;
                this.password = '';
            }
        }).catch(function(error) {
          this.$store.commit('setIsPageLoading', false);
          this.isError = true;
          alert(error.statusText);
        })
  	},
  	validateEmail: function(email) {
  		var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(email);
  	}
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #forgotPassword {
    margin-top: 15%;
  }
</style>



