<template>
    <div id="about">
      <p class="pageTitle">{{ title + ' ' + who }}</p>
      <p class="aboutText" v-html="who == 'HomeCity' ? aboutMe : aboutTysas"></p>
    </div>
</template>

<script>

import Vue from 'vue'

export default {
  props: ['who'],
  name: 'About',
  data () {
    return {
    	apiUrl: Vue.config.apiUrl,
      title: 'About',
      aboutMe: "<p>HomeCity is an application where the users can get the information about other users in a particular area. Users can sign up and edit their profile so that others in the same area can see their details.</p><p>We have many options in the edit profile like addresses, occupation, physical attributes, location, emergency contact details and so on. A user can search for the profiles only if his or her location was already been set.</p><p>And there is an option to switch the list of profiles into the current location.</p><p>The HomeCity will list the profiles based on their blood groups and occupations for now. Many more features can be added in the future updates.</p><p>Thanks for using HomeCity</p>",
      aboutTysas: "<p>Hello,</p><p>TYSAS stands for Thavanoor Yuvajana Samskarika Samithi which was formed among the people of Thavanoor, near Kondotty in Kerala.</p><p>It was organized for the social and educational services for the people who's in need. We have a strong team who is really passionate in doing social and welfare services.</p><p>The people of different religions, political parties, organizations and associations are coming under same umbrella in terms of TYSAS. So we strongly believe that we'll be able to do many thing for the society.</p><p>We do many cultural events, educational and welfare services, involve in the social issues and many more.</p><p>Thanks</p><p>TYSAS Mundilakkal</p>",
    }
  },
  methods: {

  },
  computed: {

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



